<template>
	<div class="reg">
		<!-- <div class="reg_top"> -->
			<UcNav></UcNav>
		<!-- </div> -->
		<!-- <div class="reg_bg"></div> -->
		<div class="content">
			<p>
				<router-link tag="span" to="./login">&nbsp;|&nbsp;</router-link>
				<router-link tag="span" to="./reg"></router-link>
				<!-- <span>|</span>
				<span></span> -->
			</p>
		</div>
		<!--  -->
			<UcInput label="" v-model="username"/>
			<UcInput label="" v-model="password"/>
		<!--  -->
			<UcBotton text="" @click.native="reg"/>
		<br>
    	{{mess}}
	</div>
</template>

<script>
	import UcNav from '../components/uc-nav/uc-nav'	//
	import UcBotton from '../components/uc-botton'	//
	import UcInput from '../components/uc-input'	//

	export default{
		data(){
			return {
			username: '',
			password: '',
			mess: ''
			}
		},	//
		components:{	//
			UcNav,
			UcBotton,
			UcInput
		},	//
		mounted(){},	//
		updated(){},	//
		methods:{
			upload(){
         let file=document.getElementById('file');
         file.click()
      },
      reg() {
        
        let formData = new FormData();
        formData.append('username',this.username)
        formData.append('password',this.password)
        
        let file=document.getElementById('file');
        formData.append('icon',file.files[0]);
        
        axios({
          url: '/api/reg',
          method: 'post',
          data: formData
        }).then(
          res => {
            if(res.data.err==0){
              console.log(res.data);
              this.$router.push('/login')
            }else{
              this.mess=res.data.msg
            }
          }
        )
      }
		}	//
	}
</script>

<style scoped>
	.reg{height:11.34rem;width:100%;overflow: hidden;font-size: 0.35rem;background:url(../assets/images/login_bg2.jpg) no-repeat center;}
	.reg .content{width:100%;display: flex;justify-content: center;}
	.reg .content p{font-size: 0.35rem;margin-top:3rem;}
	.reg .content span{font-size: 0.5rem;}
</style>